import {createRouter, createWebHistory} from "vue-router";

import Home from './Home.vue';
import Covid from './view/Covid19.vue';
import IGHunter from './view/IGHunter.vue';
import CGVSeat from './view/Cgvseat.vue';

const routes = [
    {name: 'Home', path : '/', component : Home},
    {name : 'covid-19-api', path : '/covid-19-api/', component : Covid, props: true},
    {name : 'IGHunter', path : '/ig-hunter/', component : IGHunter, props: true},
    {name : 'CGVSeat', path : '/cgv-seat/', component : CGVSeat, props: true},
];

const router = createRouter({
    history : createWebHistory('/portfolio/'),
    routes,
});

export default router;
