<template>
    <nav class="navbar navbar-expand-lg navbar-dark fixed-top py-3" id='navv'>
        <div class="container-fluid h-sm-100">
            <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#collapsed" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
            </button>
            <div class="collapse navbar-collapse" id="collapsed">
                <div class="navbar-nav justify-content-center mx-auto text-center text-white">
                    <a class="nav-link col text-white fw-bold" aria-current="page" href="#home">Home</a>
                    <a class="nav-link col text-white fw-bold" href="#project">Project</a>
                    <a class="nav-link col text-white fw-bold" href="#skill">Skills</a>
                    <!-- <a class="nav-link col" href="#">Testimoni</a> -->
                </div>
            </div>
        </div>
    </nav>
</template>

<script>

import 'bootstrap/dist/js/bootstrap';

export default {
    data() {
        return {
            expanded : '',
        }
    },
    methods : {
        navbarPopUp() {
            const nav = document.querySelector('nav');
            const navLink = document.querySelectorAll('.nav-link');
            const collapsed = document.querySelector('button.navbar-toggler');
            const funct = this;

            //toggle listener
            collapsed.addEventListener('click', function(){
                funct.expanded = collapsed.getAttribute('aria-expanded');
                const gls = nav.classList.contains('gls');
                // console.log(funct.expanded);

                if(funct.expanded == 'true' && gls != 'true') {
                    funct.collapse(nav, navLink);
                }
                else if(gls == 'false' || !window.pageYOffset < 150){
                    null;
                }else {
                    funct.unCollapse(nav, navLink);
                }
            });

            window.addEventListener('scroll', function() {
                
                // console.log(funct.expanded);

                //jika top lebihdari 150 add class gls dan hapus text warna hitam
                if(window.pageYOffset > 150) {
                    funct.collapse(nav, navLink);
                }
                else if(window.pageYOffset <= 150 && funct.expanded != 'true'){
                    funct.unCollapse(nav, navLink);
                }
            });
        },
        collapse(nav, navLink) {
            nav.classList.add('gls');
                navLink.forEach(function(el) {
                    el.classList.remove('text-white');
                    el.classList.add('text-dark');
            });
        },
        unCollapse(nav, navLink) {
            nav.classList.remove('gls');
                navLink.forEach(function(el) {
                    el.classList.remove('text-dark');
                    el.classList.add('text-white');
            });
        },
    },
    mounted() {
        this.navbarPopUp();
    },
}
</script>